<template>
<div class="transactions-history">

  <div class="transactions-history-name">
    <img :src="upOrDownImg">
    {{ transactionName }}
  </div>

  <div class="transactions-history-date">
    <span>{{ transactionDate[0].toUpperCase() }}</span>{{ transactionDate[1] }}
  </div> 

  <div class="transactions-history-values">
    <span class="transactions-history-coin"> {{ balanceDiff }} {{ transactionCoinName }}</span>
    <span 
      class="transactions-history-money">
      {{ historyCardData.received ? `+` : `-` }}${{ moneyBalanceDiff }}
    </span>
  </div>

</div>  
</template>






<script>

export default {
  props: {
    historyCardData: Object,
    transactionCoinName: String
  },
  
  data() {
    return {

    }
  },

  computed: {
    upOrDownImg() {
      return `./static/${this.historyCardData.received ? `up` : `down`}.svg`
    },

    transactionDate() {
      return this.historyCardData.dateConverted
    },

    transactionName() {
      return this.historyCardData.transactionName
    },

    balanceDiff() {
      return this.historyCardData.balanceDiff
    },

    moneyBalanceDiff() {
      return this.historyCardData.balanceDiffConverted
    }
  }
}

</script>






<style scoped lang="scss">
@import './src/assets/scss/mixins.scss';

.transactions-history {
  display: flex;
  padding: 14px 25px;

  &:nth-of-type(even) {
    background-color: #efefef;
  }

  @include mobile {
    padding: 14px 15px;
  }
}

.transactions-history-name {
  display: flex;
  align-items: center;
  width: calc( 60% - 100px );
  color: #111111;
  font-size: 18px;
  font-weight: 300;
  line-height: 1em;

  img {
    margin-right: 30px;
  }

  @include mobile {
    width: 60%;
    font-size: 14px;

    img {
      width: 25px;
      margin-right: 25px;
    }
  }
}

.transactions-history-date {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(#111111, 0.8);
  font-size: 16px;
  font-weight: 300;
  line-height: 1em;
  width: 100px;

  span {
    font-weight: 700;
    margin-right: 3px;
  }

  @include mobile {
    display: none;
  }
}

.transactions-history-values {
  width: 40%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
}

.transactions-history-coin {
  color: #111111;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.2em;

  @include mobile {
    font-size: 15px;
  }
}

.transactions-history-money {
  color: rgba(#111111, 0.77);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.2em;

  @include mobile {
    font-size: 10px;
  }
}

</style>


