<template>

<div class="nav-menu">
  <a href="#" class="nav-menu-link active">
    <img src="../assets/img/nav-link-ico1.svg">
    <span>Wallets</span>
  </a>
  <a href="#" class="nav-menu-link d-none d-md-flex">
    <img src="../assets/img/nav-link-ico2.svg">
    <span>Markets</span> 
  </a>
  <a href="#" class="nav-menu-link">
    <img src="../assets/img/nav-link-ico3.svg">
    <span>Buy/Sell</span>
  </a>
  <a href="#" class="nav-menu-link">
    <img src="../assets/img/nav-link-ico4.svg">
    <span>Hosting</span>
  </a>
  <a href="#" class="nav-menu-link">
    <img src="../assets/img/nav-link-ico5.svg">
    <span>Games</span>
  </a>
  <a href="#" class="nav-menu-link">
    <img src="../assets/img/nav-link-ico6.svg">
    <span>Social</span>
  </a>
</div>

</template>

<script>

export default {
  data() {
    return {

    }
  }
}

</script>

<style scoped lang="scss">
@import './src/assets/scss/mixins.scss';

.nav-menu {
  z-index: 9999;
  width: 100%;
  position: fixed;
  top: 60px;
  background-color: #dc1d1d;
  background-image: linear-gradient(to right, #37a8ff 0%, #9231ff 100%);
  display: flex;
  justify-content: center;

  @include mobile {
    top: auto;
    bottom: 0;

  }
}

.nav-menu-link {
  padding: 21px 35px 20px 35px;
  display: flex;
  align-items: center;
  color: #ffffff;
  font-size: 24px;
  font-weight: 300;
  line-height: 1em;
  background-color: transparent;
  transition: background-color .15s linear;

  img {
    height: 29px;
    margin-right: 20px;
  }

  &:hover {
    color: #ffffff;
    background-color: rgba(0,0,0,0.1);
  }

  &.active{
    font-weight: 700;
  }

  @include mobile {
    height: 50px;
    padding: 5px;
    flex-basis: 0;
    flex-grow: 1;
    align-items: center;
    justify-content: center;
    border-right: 2px solid #4e4e67;

    &:nth-last-of-type(1) {
      border-right: 0;
    }

    span {
      display: none;
    }

    img {
      margin-right: 0;
      height: 24px;
    }

    &.active{
      background-color: rgba(0,0,0,0.3);
    }
  }

  @include md {
    padding: 21px 13px 20px 13px;
    font-size: 15px;
  }

  @include lg {
    padding: 21px 15px 20px 15px;
    font-size: 24px;

    span {
      display: block;
    }
  }

  @include xl {
    padding: 21px 35px 20px 35px;
  }
}

</style>


