<template>
  <div class="container-fluid main-wrap">

    <top-navbar/>
    <nav-menu/>

    <div class="main-content">
      <div class="block-sidebar">

<!-- sidebar tabs -->        
        <div class="sidebar-tabs">
          <a href="#" class="sidebar-tab-link active">WALLETS</a>
          <a href="#" class="sidebar-tab-link">ASSETS</a>
        </div>

<!-- wallets -->
      <transition-group name="transactionsHistoryList" tag="span">
        <wallets-card  
          v-for="(wallet, walletId) in mobileView 
            ? activeMobileWalletToShow(walletsData)
            : walletsData" 
          :key="walletId" 
          :walletData="wallet" 
          @changeActiveWallet="changeActiveWallet($event)"
          @changeMobileActiveWallet="activeMobileWallet = ''"
          :activeMobileWallet="activeMobileWallet"></wallets-card>
      </transition-group>

      </div>

      <transition name="transactionsHistoryList" mode="out-in">
      <div class="block-transactions"
        v-if="mobileView && activeMobileWallet || !mobileView">

<!-- transactions search -->   
        <div class="transactions-search"> 
          <span class="transaction-heading">Transactions</span>
          <div class="transaction-input-wrap">
            <input 
              v-model="filterInputValue"
              type="text" 
              id="searchInput" 
              placeholder="Search">
          </div>
        </div>

<!-- transactions history list wrap -->
        <transition name="transactionsHistoryList" mode="out-in">
          <div 
            v-for="(wallet, walletId) in walletsData"
            :key="walletId"
            v-if="activeWalletCard == wallet.name">

<!-- transactions history -->
            <transition-group name="transactionsHistoryList" tag="span">
              <transactions-history 
                v-for="(historyCard, historyCardId) in 
                  filteredHistoryArr(walletsData[walletId].transactionsHistory)"
                :key="historyCardId"
                :historyCardData="historyCard"
                :transactionCoinName="walletsData[walletId].name">
              </transactions-history> 
            </transition-group>

          </div>
        </transition> 
      </div>
      </transition>

    </div>



  </div>
</template>







<script>

export default {
  name: 'app',
  data() {
    return {
      walletsData: null,
      activeWalletCard: 'ION',
      filterInputValue: '',
      activeMobileWallet: '',
      mobileView: window.innerWidth < 768 ? true : false
    } 
  },

  methods: {
    filteredHistoryArr(transactionsHistory) {
      return transactionsHistory.filter( transaction => {
        return `${transaction.transactionName} ${transaction.dateConverted} 
                ${transaction.balanceDiff} ${transaction.balanceDiffConverted}`.toLowerCase()
                .indexOf(this.filterInputValue.toLowerCase()) !== -1
      })
    },

    activeMobileWalletToShow(walletsData) {
//      if (!this.mobileView) {
//        return walletsData
//      }
      if (this.walletsData && this.activeMobileWallet && this.mobileView) {
        return walletsData.filter( wallet => {
          return wallet.name == this.activeMobileWallet
        })
      }
      else {
        return walletsData
      } 
    },

    setMobileView() {
      this.mobileView = window.innerWidth < 768 ? true : false
      if (!this.mobileView) this.activeMobileWallet = ''
    },

    changeActiveWallet(activeWalletName) {
      this.activeWalletCard = activeWalletName
      if (this.mobileView) this.activeMobileWallet = activeWalletName       
    }
  },

  computed: {

  },

  mounted() {
    window.addEventListener('resize', this.setMobileView)
  },

  created() {
    fetch('dataForWallets.json')
      .then( j => j.json())
      .then( walletsData => {
        let converterWalletsData = walletsData.wallets.map( walletObj => {
          walletObj.convertedBalance = (walletObj.balance * walletObj.convertingRate).toFixed(5);

          walletObj.transactionsHistory.map( historyObj => {
            let monthNames = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
            let newDate = new Date(historyObj.date);

            historyObj.balanceDiffConverted 
              = (Math.abs(historyObj.balanceDiff * walletObj.convertingRate)).toFixed(5);

            historyObj.dateConverted = [monthNames[newDate.getMonth()], newDate.getDay()]
          }) 
          return walletObj
        })
        return this.walletsData = converterWalletsData
      })
  },
}

</script>








<style lang="scss">
@import './src/assets/scss/mixins.scss';
  
@-ms-viewport {
  width: device-width;
}

html {
  box-sizing: border-box;
  -ms-overflow-style: scrollbar;
}

*,
*::before,
*::after {
  box-sizing: inherit;
} 
 
.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
  width: 100%;
}

.container-fluid {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
  width: 100%;
}
  
body {
  overflow-x: hidden;
  position: relative;
  font-weight: 300;
  font-family: 'Roboto', sans-serif;
} 

img {
  flex-shrink: 0; 
} 

.container-fluid {
  padding-left: 0;
  padding-right: 0;
}

a { 
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
  color: inherit;
}

p {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.3em;
}

.main-wrap {
  min-height: 100vh;
  padding-top: 130px;
  padding-bottom: 50px;
  overflow: hidden;

  @include mobile {
    padding-top: 55px; 
    padding-bottom: 50px;
  }
}

.main-content {
  position: relative;
  margin: 0 auto;
  display: flex;
  margin-bottom: 0;
  background-color: #f6f6f6;
  max-width: 1185px;
  box-shadow: 0 5px 25px rgba(#000000, 0.2);

  &:before {  
    content: url('assets/img/main-content-before.png');
    position: absolute;
    top: 0;
    right: 100%;
  }

  &:after {  
    content: url('assets/img/main-content-after.png');
    position: absolute;
    top: 0;
    left: 100%;
  }

  @include mobile {
    flex-wrap: wrap;
  }
}

.block-sidebar {
  width: 245px;
  border-right: 1px solid rgba(#252525, 0.3);

  @include mobile {
    padding-top: 40px;
    width: 100%;
    border-right: 0;
  }
}

.sidebar-tabs {
  display: flex;
  border-bottom: 1px solid rgba(#252525, 0.3);

  @include mobile  {
    position: fixed;
    z-index: 9999;
    top: 55px;
    width: 100%;
    background-color: #f7f7f7;
  }

  .sidebar-tab-link {
    height: 45px;
    flex-basis: 0;
    flex-grow: 1;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgba(#111111, 0.5);
    font-size: 14px;
    font-weight: 300;
    line-height: 1em;
    background-color: transparent;
    transition: background-color .15s linear;

    &.active {
      font-weight: 700;
      color: rgba(#111111, 0.7);
    }

    &:hover {
      background-color: rgba(#000000, 0.1);
    }

    &:nth-of-type(1) {
      border-right: 1px solid rgba(#252525, 0.3);
    }

    @include mobile {
      height: 40px;

    }
  }
}



.block-transactions {
  width: calc( 100% - 245px );

  @include mobile {
    width: 100%;
  }
}

.transactions-search {
  display: flex;
  align-items: center;
  height: 46px;
  border-bottom: 1px solid rgba(#252525, 0.3);
}

.transaction-heading {
  color: #111111;
  font-size: 16px;
  font-weight: 300;
  line-height: 1em;
  width: 140px;
  text-align: center;
}

.transaction-input-wrap {
  width: calc( 100% - 140px );
  height: 100%;
  padding: 10px 50px 8px 10px;
  position: relative;

  input {
    height: 100%;
    width: 100%;
    border: 0;
    background-color: transparent;
    outline: 0;
    color: rgba(#111111, 0.4);
    font-size: 16px;
    font-weight: 300;

    &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
      color: rgba(#111111, 0.4);
      font-size: 16px;
      font-weight: 300;
      font-style: italic;
      line-height: 1em;
      text-align: right;
    }
    &::-moz-placeholder { /* Firefox 19+ */
      color: rgba(#111111, 0.4);
      font-size: 16px;
      font-weight: 300;
      font-style: italic;
      line-height: 1em;
      text-align: right;
    }
    &:-ms-input-placeholder { /* IE 10+ */
      color: rgba(#111111, 0.4);
      font-size: 16px;
      font-weight: 300;
      font-style: italic;
      line-height: 1em;
      text-align: right;
    }
    &:-moz-placeholder { /* Firefox 18- */
      color: rgba(#111111, 0.4);
      font-size: 16px;
      font-weight: 300;
      font-style: italic;
      line-height: 1em;
      text-align: right;
    }
  }

  &:after {
    content: url('assets/img/search.svg');
    position: absolute;
    display: inline-block;
    top: 50%;
    right: 25px;
    transform: translateY(-50%);
  }

  @include mobile {

    &:after {
      right: 15px;
    }
  } 
}
 









.container-fluid {
  .mb-10 {
    margin-bottom: 10px;
  }

  .mb-15 {
    margin-bottom: 15px;
  }

  .mb-20 {
    margin-bottom: 20px;
  }

  .mb-25 {
    margin-bottom: 25px;
  }

  .mb-30 {
    margin-bottom: 30px;
  }

  .mb-40 {
    margin-bottom: 40px;
  }

  .mb-50 {
    margin-bottom: 50px;
  }

  .mb-60 {
    margin-bottom: 60px;
  }

  .text-center {
    text-align: center;
  }

  .text-right {
    text-align: right;
  }

  .img-responsive {
    display: block;
    max-width: 100%;
    flex-shrink: 0;
    height: auto;
  }

  .mx-auto {
    margin-right: auto;
    margin-left: auto;
  }

  .d-none {
    display: none;
  }

  .d-block {
    display: block;
  }

  @include mobile {
    .xs-text-center {
      text-align: center;
    }

    .xs-text-left {
      text-align: left;
    }

    .mx-xs-auto {
      margin-left: auto;
      margin-right: auto;
    }
  }

  @include md {
    .d-md-none {
      display: none;
    }

    .d-md-block {
      display: block;
    } 

    .d-md-flex {
      display: flex;
    }
  }
}


.transactionsHistoryList-enter-active {
  transition: all 0.4s;
}

.transactionsHistoryList-leave-active {
  transition: all 0.4s;
}

.transactionsHistoryList-leave-to {
  opacity: 0;
  transform: translateY(5px);
}

.transactionsHistoryList-enter {
  opacity: 0;
  transform: translateY(-5px);
}
 
</style>



