<template>
<div class="nav-top">

  <div class="nav-top-logo">
    <a href="#">
      <img src="../assets/img/logo.png" class="img-responsive d-none d-md-block">
      <img src="../assets/img/logo-mobile.png" class="img-responsive d-block d-md-none">
    </a>
  </div>

  <div class="nav-top-total">
    <span>ION = $2.55</span>
    <span class="nav-top-divider"></span>
    <span>Total $ = $23,658.55</span>
  </div>

  <div class="nav-top-setting">
    <a href="#" class="nav-top-setting-user">Usersnamehead2017</a>
    <a href="#" class="nav-top-setting-cog">
      <img src="../assets/img/cog.svg" class="img-responsive">
    </a>
  </div>
  
</div> 
</template>

<script>

export default {
  data() {
    return {

    }
  }
}   

</script>

<style scoped lang="scss">
@import './src/assets/scss/mixins.scss';

.nav-top {
  z-index: 9999;
  width: 100%;
  position: fixed;
  top: 0;
  display: flex;
  align-items: center; 
  background-color: #111111;
  padding: 10px 25px;

  @include mobile {
    justify-content: space-between;
  }
}
 
.nav-top-logo {
  flex-grow: 2;

  img.img-responsive {
    width: 100%;
    max-width: 127px;
  }

  @include mobile {
    flex-grow: 0;

    img.img-responsive {
      width: 100%;
      max-width: 36px;
    }
  }
}

.nav-top-total {
  color: #ffffff;
  font-size: 13px;
  line-height: 1.2em;
  display: flex;
  align-items: center;
  margin-right: 80px;

  @include mobile {
    flex-wrap: wrap;
    margin-right: 10px;
    margin-left: 10px;
    span {
      text-align: center;
      width: 100%;

      &:nth-of-type(1) {
        margin-bottom: 5px;
      }
    }
  }
}

.nav-top-divider {
  display: inline-block;
  height: 30px; 
  border-right: 1px solid #ffffff;
  margin: 0 15px;

  @include mobile {
    display: none;
  }
}

.nav-top-setting {
  display: flex;
  align-items: center;
  color: #2499ca;
  font-size: 18px;
  line-height: 1.2em; 
}

.nav-top-setting-cog {
  margin-left: 20px;
  transition: transform .15s linear;

  &:hover {
    transform: rotate(-45deg);
  }

  @include mobile {
    margin-left: 0;
  }
}

.nav-top-setting-user {
  transition: color .15s linear;

  &:hover {
    color: #ffffff;
  }

  @include mobile {
    display: none;
  }
}
</style>


