import Vue from 'vue'
import App from './App.vue'
import topNavbar from './components/topNavbar.vue'
import navMenu from './components/navMenu.vue'
import walletsCard from './components/walletsCard.vue'
import transactionsHistory from './components/transactionsHistory.vue'

import reset from 'reset-css';

Vue.component('topNavbar', topNavbar)
Vue.component('navMenu', navMenu)
Vue.component('walletsCard', walletsCard)
Vue.component('transactionsHistory', transactionsHistory)

new Vue({
  el: '#app',
  render: h => h(App)
})



