<template>
  
<div :class="{ 
        'sidebar-wallets': true, 
        'activeMobileWallet': activeMobileWalletorNot 
      }">
  <a href="index.html" 
    class="sidebar-wallets-back"
    @click.prevent="$emit('changeMobileActiveWallet')">
    <img src="../assets/img/arrow-left.png">
  </a>
  <div class="sidebar-wallets-inner">
    <a href="mobile-transactions.html" 
      @click.prevent="$emit('changeActiveWallet', coinName)" 
      class="coin-name open-history-link" 
      :data-id-to-open="walletId">
      <img :src="walletImgSrc">
      {{ coinName }}
    </a>
    <div class="sidebar-wallets-links">
      <a href="#">
        <img src="../assets/img/paper-plane.svg">
      </a>
      <a href="#">
        <img src="../assets/img/qrcode.svg">
      </a>
    </div>
  </div>
  <span class="sidebar-wallets-count"> {{ coinBalance }} ~ ${{ moneyBalance }}</span>
</div>

</template>
 
 





<script>

export default {
  props: {
    walletData: Object,
    activeMobileWallet: String
  },
  data() {
    return {

    }
  },

  computed: {
    walletImgSrc() {
      return `/static/coins/${this.coinName}.png`
    },

    walletId() {
      return `#history-${this.coinName.toLowerCase()}`
    },

    coinName() {
      return this.walletData.name
    },

    coinBalance() {
      return this.walletData.balance
    },

    moneyBalance() {
      return this.walletData.convertedBalance
    },

    activeMobileWalletorNot() {
      return this.activeMobileWallet == this.coinName
    }
  }
}

</script>






<style scoped lang="scss">
@import './src/assets/scss/mixins.scss';

.activeMobileWallet {
  &.sidebar-wallets {
    padding-left: 50px;
  }

  .sidebar-wallets-back {
    transform: translateY(-50%) translateX(0);
    opacity: 1;
  }
}

.sidebar-wallets {
  padding: 10px 15px;
  position: relative;
  transition: padding-left .15s linear;

  &:nth-of-type(even) {
    background-color: #efefef;
  }
}

.sidebar-wallets-inner {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.coin-name {
  display: flex;
  align-items: center;
  color: #111111;
  font-size: 19px;
  font-weight: 300;
  line-height: 1em;

  img {
    margin-right: 15px;
    max-width: 24px;
    max-height: 24px;
  }
}


.sidebar-wallets-count {
  color: rgba(#111111, 0.66);
  font-size: 13px;
  font-weight: 300;
  line-height: 1em;
}

.sidebar-wallets-links {
  display: flex;
  align-items: center;
  
  a {
    display: inline-block;
    transform: scale(1);
    transition: transform .15s linear;

    &:hover {
      transform: scale(1.1);
    }

    &:nth-of-type(1) {
      margin-right: 30px;
    }
  }
}

.sidebar-wallets-back {
  display: none;

  @include mobile {
    position: absolute;
    top: 50%;
    left: 15px;
    display: inline-block;
    transform: translateY(-50%) translateX(-100%);
    opacity: 0;
    transition: transform .15s linear,
                opacity .15s linear;

    img {
      width: 15px;
    }
  } 
}

</style>


